/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import WidgetCard from 'components/Cards/WidgetCard';
import { useStaticQuery, graphql, Link } from 'gatsby';
import { Chip, List, ListItem, ListItemText, ListItemSecondaryAction, makeStyles } from '@material-ui/core';
import { Colors } from 'utils/colors';
import { useCallback } from 'react';
import { linkEvent } from 'utils/loggers';
import { Group } from 'models/post';
import { toKebabCase } from 'utils/string';

const CategoriesCard = () => {
  const chipStyles = makeStyles({
    root: {
      borderRadius: '4px',
      fontSize: '0.6rem',
      height: 'auto',
      padding: '0.3rem 0',
      backgroundColor: Colors.Grey['200'],
    },
  })();
  const {
    allMarkdownRemark: { group: categories },
  }: Group = useStaticQuery(graphql`
    query CategoriesQuery {
      allMarkdownRemark {
        group(field: frontmatter___categories) {
          fieldValue
          totalCount
        }
      }
    }
  `);

  const handleCategoryClick = useCallback((url: string) => {
    linkEvent(url, '카테고리 링크 클릭');
  }, []);

  return (
    <WidgetCard title="카테고리">
      <List component="nav" dense={true}>
        {categories.map(({ fieldValue, totalCount }, index) => (
          <Link
            key={index}
            to={`/categories/${toKebabCase(fieldValue)}`}
            css={css`
              color: inherit;
              text-decoration: none;
            `}
          >
            <ListItem
              button
              css={css`
                list-style: none;
                margin: 0 0 1rem 0;
                padding: 0;
                border-radius: 4px;
                overflow: hidden;
              `}
              onClick={() => handleCategoryClick(`/categories/${toKebabCase(fieldValue)}`)}
            >
              <ListItemText>{fieldValue}</ListItemText>
              <ListItemSecondaryAction>
                <Chip className={chipStyles.root} label={totalCount} size="small" />
              </ListItemSecondaryAction>
            </ListItem>
          </Link>
        ))}
      </List>
    </WidgetCard>
  );
};

export default CategoriesCard;
